/* Sets form */

.setsBox .input-num {
  border-radius: 3px;
  padding-left: 5px;
  font-size: 16px;
  width:30%;
  height: 40px;
  box-shadow:  0 5px 25px rgba(0,0,0,.5);
  outline: none;
  border: 0;
  margin: 5px;
}

.setsBox {
  display: inline-block;
  position: relative;
}

.setsInput {
  text-align: center;
  padding: 10px;
  border: 2 px solid grey;
}

/* Text Area Styles */
.textArea {
  text-align: center;
  width: auto;
  padding: 10px;
}

.form-text-normal {
  padding: 10px;
  border-radius: 3px;
  font-size: 16px;
  outline: none;
  width: 80%;
  box-shadow:  0 5px 25px rgba(0,0,0,.5);
  border:0;
  height: 20%;
}


/* Select Box Styles */

.muscleSelect {
  width: 100%;
  border: 1px solid grey;
  padding: .8rem 3rem .8rem .5rem;
  border-radius: 3px;
  font-size: 16px;
  box-shadow:  0 5px 25px rgba(0,0,0,.5);
    -webkit-appearance: none;
    -moz-appearance: none;
      appearance: none;
  outline: none;
  cursor: pointer;
}
.muscleSelect::selection {
  box-shadow:  0 5px 25px rgba(0,0,0,1);
}
.container:after {
  content: 'keyboard_arrow_down';
  font-family: 'material icons';
  font-size: 35px;
  position: absolute;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  pointer-events: none;
  right: 10px;
  top: 25%;
}
.container {
  padding: 10px;
  border: 2 px solid grey;
  position: relative;
}

.dropdown {
  display: inline-block;
  position: relative;
}


/* Form Buttons */
.logWorkout {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}
.form-btn {
  float: right;
  border: 0;
  background-color: black;
  padding: 12px 50px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 16px;
  color: #fff;
}
.form-btn:hover{
  background:#ed213a;
  background: -webkit-gradient(linear, left top, right top, from(#ed213a), to(#93291e));
  background: linear-gradient(to right, #ed213a, #93291e)
}



@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 568px) { 

  .border {
    margin: 10px auto;
  }
  .addExercise-form{
    overflow: hidden;
    background: rgba(0, 0, 0, .6);
    margin-top: 20px;
    margin-right: 10px;
