  }
  .setsBox .input-num {
    padding: .1rem .5rem .3rem .5rem;
    border-radius: 2px solid grey;
    font-size: 16px;
    width: auto;
    margin-bottom: 5px;
  }
  .setsInput {
    text-align: center;
    padding: 10px;
    border: 2 px solid grey;
    position: relative;
  }
  .textArea {
    position: relative;
  }
  .form-text-normal {
    padding: 15px;
  }
  .logWorkout {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
  }
  .form-btn {
    border: 0;
    font-size: 16px;
    width: 50%;
    background-color: black;
    padding: 12px;
    border-radius: 15px;
